import { Accordion, Button, Form, InputGroup, Spinner } from 'react-bootstrap';
import { useState, useEffect } from 'react';

import axios from 'axios';

import './InsMessage.css';


function InsMessage() {
  const [clientMessage, setClientMessage] = useState("");
  const [clientEmail, setClientEmail] = useState("");
  const [sendContactMsgStatus, setSendContactMsgStatus] = useState(false);
  const [validated, setValidated] = useState(false);
  const [sentSuccess, setSentSuccess] = useState(false);

  const sendContactMsg = (event) => {
    setSendContactMsgStatus(true);

    const form = event.currentTarget;
    event.preventDefault();

    if (form.checkValidity() === false) {
      event.stopPropagation();
    }

    setValidated(true);
  
    axios.post('https://py-toolbox.herokuapp.com/contactMe', {
      clientMsg: encodeURIComponent(clientMessage),
      clientEmail: encodeURIComponent(clientEmail)
    })
    .then(function (response) {
      setSendContactMsgStatus(false);
      setClientMessage("");
      setClientEmail("");

      setSentSuccess(true);
      setValidated(false);

      const timer = setTimeout(() => {
        setSentSuccess(false);
        clearTimeout(timer);
      }, 5000);
    })
    .catch(function (error) {
      setSendContactMsgStatus(false);
    });
  }

  return (
      <div className="msgModal">
          <Accordion>
            <Accordion.Item eventKey="0">
              <Accordion.Header>Contact Me!&nbsp;</Accordion.Header>
              <Accordion.Body>
                <Form validated={validated} onSubmit={sendContactMsg} style={{display: sentSuccess ? "none" : ""}}>
                  <Form.Control as="textarea" required placeholder="Leave a message here..." value={clientMessage} onChange={(event) => setClientMessage(event.target.value)} style={{height: "15rem"}} />
                  <br/>
                  <Form.Control required size="sm" type="email" value={clientEmail} onChange={(event) => setClientEmail(event.target.value)} placeholder="Your Email" />
                  <br/>
                  <Button variant="outline-secondary" type="submit" >SEND NOW &nbsp;
                    <Spinner className="sendSpin" as="span" animation="border" variant="dark" size="sm" role="status" aria-hidden="true" style={{display: sendContactMsgStatus ? "" : "none"}} />
                  </Button>
                </Form>

                <div style={{display: sentSuccess ? "block" : "none"}}>
                  <span style={{color: "darkgreen"}}>Message sent successfully!<br/>I'll reply to you ASAP. :)</span>
                </div>
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
      </div>
  );
}

export default InsMessage;